/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 10px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background-color: #000000;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: rgb(16, 16, 16);
}

.coaches-database-table-container,
.student-database-table-container {
  padding: 0px 30px;
  display: block;
  overflow-x: auto;
  white-space: nowrap;
  width: 100%;
  margin-top: 0px;
}

table {
  background-color: rgb(18, 17, 17);
}

.table > :not(caption) > * > * {
  padding: 15px 20px;
}

thead {
  background-color: #42e091;
  color: rgb(19, 19, 19);
}

th {
  font-size: 18px;
  font-weight: 680;
}

td {
  font-size: 17px;
  font-weight: 450;
  color: rgba(255, 255, 255, 0.749);
}

.table-row:hover {
  background-color: rgba(0, 0, 0, 0.916);
}
