/* 2196f3 */
.blue {
  color: #2196f3;
}

.login-page-container {
  position: relative;
  height: 700px;
  width: 100%;
  background-image: url("../../components/assets/login-background.jpg");
  background-size: 2000px;
}

.login-page-container-content {
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 98%;
  margin: 0 auto;
}

.login-btn {
  padding: 10px 30px;
  outline: none;
  background-color: #145a94;
  border: 1px solid #145a94;
  -webkit-box-shadow: 0px 0px 35px 0px #145a94;
  -moz-box-shadow: 0px 0px 35px 0px #145a94;
  box-shadow: 0px 0px 35px 0px #145a94;
  border-radius: 3px;
  color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.login-btn:hover {
  transform: scale(110%);
  background-color: rgb(2, 2, 2);
  -webkit-box-shadow: 0px 0px 21px -7px rgba(33, 150, 243, 1);
  -moz-box-shadow: 0px 0px 21px -7px rgba(33, 150, 243, 1);
  box-shadow: 0px 0px 21px -7px rgba(33, 150, 243, 1);
}
