.custom-admin-navbar {
  background-color: rgb(0, 0, 0);
}

.admin-greeting {
  color: rgb(216, 216, 216);
}

.admin-offcanvas {
  background-color: #000000f9;
  color: white;
}

a {
  text-decoration: none;
}

.admin-navbar-list {
  color: white;
  list-style: none;
  padding: 10px 0px;
  font-size: 20px;
}

.admin-navbar-list:hover {
  transition: all 0.6s ease;
  color: #03c765;
}

.active {
  font-weight: bolder;
  color: #00e472;
}

.admin-logout-btn {
  background-color: #00ff80;
  color: black;
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 7px 15px;
  margin-top: 50px;
  font-weight: 700;
}
